import { Component, OnInit, HostBinding, Input, Output, EventEmitter } from '@angular/core';
import { DomSanitizer, SafeStyle } from '@angular/platform-browser';

import { FilterService } from './../services/filter.service';
import { FilterCondition, Compare } from '@farris/ui-common/types';

@Component({
    selector: 'filter-editor',
    template: `
        <filter-tabs
            class="f-utils-fill-flex-column"
            [showCode]="showCode"
            [showSql]="showSql"
            [columns]="columns"
            [height]="height"
            [enableExpress]="enableExpress"
            [enableFrontExpress]="enableFrontExpress"
            [conditions]="conditions"
            [fieldEditor]="fieldEditor"
            (showExpress)="onShowExpress($event)"
            (selectTabChange)="onTabChange($event)">
        </filter-tabs>
    `
})

export class FilterEditorComponent implements OnInit {
    tabIndex = 0;
    @Input() columns = [];
    @Input() conditions: FilterCondition[] = [];
    @Input() height: number;
    @Input() showCode = false;
    @Input() showSql = false;
    @Input() enableExpress = false;
    @Input() enableFrontExpress = false;
    @Input() fieldEditor  = { type: 'select', options: {}};

    @Output() showExpress = new EventEmitter();
    @HostBinding('class') cls = 'f-utils-flex-column f-utils-absolute-all';

    constructor(private sanitizer: DomSanitizer, private filterService: FilterService) {
        this.filterService.conditionsChanged.subscribe(conditionList => {
            this.conditions = conditionList;
        });
    }

    ngOnInit() {
        if (this.conditions && this.conditions.length) {
            this.conditions = this.conditions.map(con => {
                const cp = parseInt('' + con.compare, 10);
                if (cp === Compare.In || cp === Compare.NotIn) {
                    con.value = con.value.replace(/\r\n/g, ',');
                }
                return con;
            });
        }
    }


    onTabChange(index: number) {
        this.tabIndex = index;
    }

    onShowExpress($event) {
        this.showExpress.emit($event);
    }
}
