import { Injectable, Optional } from '@angular/core';
import { Subject } from 'rxjs';
import { FilterCondition, Compare, FilterRelation, CompareOperators } from '@farris/ui-common/types';
import { CommonUtils } from '@farris/ui-common';
@Injectable()
export class FilterService {

    conditionsChanged = new Subject<FilterCondition[]>();
    commonUtils: CommonUtils;
    constructor(@Optional() utils: CommonUtils) {
        this.commonUtils = utils || new CommonUtils();
    }

    buildSqlWhere(c: any) {
        return this.commonUtils.buildSqlWhere(c);
    }
}
